export default class Thing {
	constructor(x, y, vx, vy, mass, af, drag, _color, letter, rx, ry, at, sk) {

    this.x = x
    this.y = y
    this.vx = vx
    this.vy = vy
    this.mass = mass
    this.af = af
    this.drag = drag
    this.color = _color
		this.letter = letter
		this.at = at
		this.pConfig = {k: -25, b: -2}
		this.targetPos = {}
		this.rx = rx
		this.ry = ry
		this._t = Date.now()
		this.now
		this.t
		this.sk = sk
	}

  isBorderMouseIntersecting() {
  	return this.sk.dist(this.rx, this.ry, this.sk.mouseX, this.sk.mouseY) < this.at/2 && this.sk.dist(this.rx, this.ry, this.sk.mouseX, this.sk.mouseY)
  }
	
 	handleTime() {
		this.now = Date.now()
		this.t = this.now - this._t
		this._t = this.now
	}
	
  transitionPhysics() {
    const spring_x = this.pConfig.k * (this.x - this.targetPos.x)
    const damper_x = this.pConfig.b * this.vx
    this.ax = (spring_x + damper_x) / this.mass
    this.vx += this.ax * (this.t/1000)
    this.x += this.vx * (this.t/2000)

    const spring_y = this.pConfig.k * (this.y - this.targetPos.y)
    const damper_y = this.pConfig.b * this.vy
    this.ay = (spring_y + damper_y) / this.mass
    this.vy += this.ay * (this.t/1000)
    this.y += this.vy * (this.t/2000)
	}	
  
  setObjectTarget() {
    // -- last number in this map below is the attraction force. decrease to increase!
    let dok = this.isBorderMouseIntersecting() && this.sk.map(this.isBorderMouseIntersecting(), 0, this.at/this.drag, 0, this.af)
	
    this.targetPos.x = this.isBorderMouseIntersecting() ? this.sk.lerp(this.sk.mouseX, this.rx, dok) : this.rx
    this.targetPos.y = this.isBorderMouseIntersecting() ? this.sk.lerp(this.sk.mouseY, this.ry, dok) : this.ry
	}
  
  show() {

		this.sk.fill(this.color)
		this.sk.text(this.letter, this.x, this.y)
  }
}


