import Thing from './Thing'
import { h, render, Component } from 'preact'
import Font from '../../assets/Helvetica-CondensedBold.woff'

export default function p5sketch(sk) { 
  let things = []
  let font

  function setupText() {
    const splitz = 'HELLO THERE'.split('')
    const af = 1
    let x = 100
    let y = 100

    splitz.forEach(l => {
      things.push(new Thing(x, y, 0, 0, 0.3, 2, af, sk.color(255, 0, 0), l, x, y, 250, sk))
      things.push(new Thing(x, y, 0, 0, 0.3, 2, af*0.85, sk.color(0, 255, 0), l, x, y, 250, sk))
      things.push(new Thing(x, y, 0, 0, 0.3, 2, af*0.65, sk.color(0, 0, 255), l, x, y, 250, sk))

      x += 50;
    })
  }

  sk.preload = () => {
    font = sk.loadFont(Font)
  }
  
  sk.setup = () => {
  
    sk.createCanvas(700, 200)
    sk.pixelDensity(1)
    sk.blendMode(sk.DIFFERENCE)
    sk.textAlign(sk.CENTER, sk.CENTER)
    sk.textSize(100)
    sk.textFont(font)
    setupText()
  }

  sk.draw = () => {
    sk.clear()
    sk.background('#FAFAFA')

    things.forEach(thing => {
      thing.handleTime()
      thing.setObjectTarget()
      thing.transitionPhysics()
      thing.show()
    })
  }
}




